'use client';

import Link from 'next/link';
import { useEffect, useState } from 'react';
import { fetchPosts } from '@/lib/api';
import { Card } from '@/components/ui/card';
import { Button } from '@/components/ui/button';
import { Avatar, AvatarFallback, AvatarImage } from '@/components/ui/avatar';
import { Heart, MessageCircle, Share2, Bookmark, Image as ImageIcon } from 'lucide-react';

interface Post {
  id: number;
  Title: string;
  Content: Array<{
    type: string;
    children: Array<{
      type: string;
      text: string;
    }>;
  }>;
  Slug: string;
  createdAt: string;
  updatedAt: string;
  publishedAt: string;
  Thumbnail?: {
    id: number;
    url: string;
  };
}

function getPlainText(content: Post['Content']): string {
  return content
    .map(block => 
      block.children
        .map(child => child.text)
        .join('')
    )
    .join('\n');
}

export default function HomePage() {
  const [posts, setPosts] = useState<Post[]>([]);
  const [page, setPage] = useState(1);
  const [loading, setLoading] = useState(false);
  const [hasMore, setHasMore] = useState(true);

  const loadPosts = async () => {
    if (loading || !hasMore) return;
    
    setLoading(true);
    try {
      const newPosts = await fetchPosts(page);
      if (newPosts && newPosts.length > 0) {
        setPosts(prev => [...prev, ...newPosts]);
        setPage(prev => prev + 1);
      } else {
        setHasMore(false);
      }
    } catch (error) {
      console.error('Error loading posts:', error);
    } finally {
      setLoading(false);
    }
  };

  useEffect(() => {
    loadPosts();
  }, []);

  useEffect(() => {
    const handleScroll = () => {
      if (
        window.innerHeight + document.documentElement.scrollTop
        === document.documentElement.offsetHeight
      ) {
        loadPosts();
      }
    };

    window.addEventListener('scroll', handleScroll);
    return () => window.removeEventListener('scroll', handleScroll);
  }, [loading, hasMore]);

  return (
    <main className="min-h-screen bg-transparent">
      {/* Create Post */}
      <div className="border-b border-zinc-800 p-4">
        <div className="flex gap-4">
          <Avatar className="h-10 w-10">
            <AvatarImage src="/avatar.png" alt="User" />
            <AvatarFallback>U</AvatarFallback>
          </Avatar>
          <div className="flex-1">
            <textarea
              placeholder="What's happening?"
              className="w-full bg-transparent text-white placeholder:text-zinc-500 resize-none focus:outline-none"
              rows={3}
            />
            <div className="flex items-center justify-between mt-4">
              <div className="flex gap-2">
                <Button variant="ghost" size="icon" className="text-primary hover:text-primary/90">
                  <ImageIcon className="h-5 w-5" />
                </Button>
              </div>
              <Button className="bg-primary text-white hover:bg-primary/90 rounded-full px-4">
                Post
              </Button>
            </div>
          </div>
        </div>
      </div>

      {/* Posts Feed */}
      <div className="divide-y divide-zinc-800">
        {posts && posts.length > 0 ? (
          posts.map((post: Post) => (
            <article key={post.id} className="p-4 hover:bg-zinc-900/50 transition-colors">
              <div className="flex gap-4">
                <Avatar className="h-10 w-10">
                  <AvatarImage src="/avatar.png" alt="Deep Decrypt" />
                  <AvatarFallback>DD</AvatarFallback>
                </Avatar>
                <div className="flex-1">
                  <div className="flex items-center gap-2 mb-1">
                    <span className="font-bold">Deep Decrypt</span>
                    <span className="text-zinc-400 text-sm">Â·</span>
                    <span className="text-zinc-400 text-sm">
                      {new Date(post.createdAt).toLocaleDateString('en-US', {
                        year: 'numeric',
                        month: 'long',
                        day: 'numeric'
                      })}
                    </span>
                  </div>
                  <Link href={`/blog/${post.Slug}`} className="block">
                    <h3 className="text-lg font-semibold mb-2 hover:text-primary transition-colors">
                      {post.Title}
                    </h3>
                    <p className="text-zinc-200 text-sm mb-3">
                      {getPlainText(post.Content).slice(0, 180)}...
                    </p>
                  </Link>
                  {post.Thumbnail && (
                    <div className="rounded-xl overflow-hidden mb-3">
                      <img
                        src={`${process.env.NEXT_PUBLIC_API_URL}${post.Thumbnail.url}`}
                        alt={post.Title}
                        className="object-cover w-full h-48"
                      />
                    </div>
                  )}
                  <div className="flex items-center gap-6 text-zinc-400">
                    <button className="flex items-center gap-2 hover:text-primary transition-colors">
                      <MessageCircle className="h-5 w-5" />
                      <span className="text-sm">Comment</span>
                    </button>
                    <button className="flex items-center gap-2 hover:text-red-500 transition-colors">
                      <Heart className="h-5 w-5" />
                      <span className="text-sm">Like</span>
                    </button>
                    <button className="flex items-center gap-2 hover:text-primary transition-colors">
                      <Share2 className="h-5 w-5" />
                      <span className="text-sm">Share</span>
                    </button>
                    <button className="flex items-center gap-2 hover:text-primary transition-colors ml-auto">
                      <Bookmark className="h-5 w-5" />
                    </button>
                  </div>
                </div>
              </div>
            </article>
          ))
        ) : (
          <p className="p-4 text-zinc-400">No posts found. Please check back later for new content.</p>
        )}
        {loading && (
          <div className="p-4 text-center text-zinc-400">
            Loading more posts...
          </div>
        )}
      </div>
    </main>
  );
}
